import { Component } from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {Router} from "@angular/router";
import * as firebase from "firebase/app";
@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {
  session;
  questions = [];
  answers = [];
  fname = '';
  lname = '';
  requireName = true;
  maskPass = true;
  tempPass = true;
  formValid = false;
  passMessage;
  failMessage;
  failed = [];
  regKey = '';
  constructor(public http:HttpClient, public router: Router) {
    this.regKey = this.getParameterByName('regKey',null);
    this.maskPass = this.getParameterByName('mask',null) == 'true';
    this.tempPass = this.getParameterByName('temp',null) == 'true';
    this.failMessage = this.getParameterByName('failM',null);
    this.passMessage = this.getParameterByName('passM',null);
    this.requireName = this.getParameterByName('reqName',null) == 'true';
    this.session = this.getParameterByName('session',null);
    firebase.database().ref('results/' + this.regKey).set(JSON.stringify({ 'init': true })+'*'+Date.now());
    firebase.database().ref('questions/'+this.regKey).on('value', (snapshot)=> {
      let result = (snapshot.val() && snapshot.val());
      try{
        this.questions = result.split('\n').filter(a=>{if(a.trim()!='') return a});
        for(let i in this.questions){
          this.answers.push(null);
        }
      } catch (e) {}
    });
  }


  getParameterByName(name, url) {
    if (!url) url = window.location.href;
    name = name.replace(/[\[\]]/g, "\\$&");
    let regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
        results = regex.exec(url);
    if (!results) return null;
    if (!results[2]) return '';
    return decodeURIComponent(results[2].replace(/\+/g, " "));
  }

  answered(i, e){
    console.log(i,e.detail.value);
    let t = true;
    this.answers.splice(i, 1, e.detail.value === 'y');
    for(let val of this.answers){
      if(val==null){
        t = false;
      }
    }
    console.log(t, this.answers)
    this.formValid = t;
  }

  finish(){
    let failedQuestions = [];
    if(this.formValid){
      if(this.fname && this.lname || !this.requireName){
        let t = true;
        for(let val in this.answers){
          if(this.answers[val]===true){
            t = false;
            failedQuestions.push(val)
          }
        }
        let result = {'first_name':this.fname, 'last_name': this.lname, pass: t && this.maskPass && this.tempPass, session: this.session, failedQuestions:failedQuestions};
        console.log(result)
        firebase.database().ref('results/' + this.regKey).set(JSON.stringify(result)+'*'+Date.now());
        //todo output result
        alert(t&&this.maskPass&&this.tempPass?this.passMessage:this.failMessage)
        this.router.navigate(['/ad']);

      }else{
        alert('Please input your name.')
      }
    }
    else {
      alert('Please answer all questions.')
    }
  }
}
